import React, { Component } from "react";
import { Card, Button, Row, Container, Col } from "react-bootstrap";
import { Redirect } from "react-router-dom";
import runtimeEnv from "@mars/heroku-js-runtime-env";
const env = runtimeEnv();
export default class Home extends Component {
  state = {
    redirect: false,
    to: "",
  };
  setRedirect = (url) => {
    this.setState({
      redirect: true,
      to: url,
    });
  };
  renderRedirect = () => {
    console.log(this.state.redirect);
    if (this.state.redirect) {
      return <Redirect to={this.state.to} />;
    }
  };
  componentDidMount() {
    const favicon = document.getElementById("FavIcon");
    if (
      env.REACT_APP_FAVICONURL !== undefined ||
      env.REACT_APP_FAVICONURL !== ""
    )
      favicon.href = `${env.REACT_APP_FAVICONURL}`;
    const title = document.getElementById("AppTitle");
    if (env.REACT_APP_TITLE !== undefined || env.REACT_APP_TITLE !== "")
      title.innerText = `${env.REACT_APP_TITLE}`;
    else {
      title.innerText = "App";
    }
  }
  render() {
    return (
      <div>
        {this.renderRedirect()}
        <Card
          style={{
            position: "absolute",
            left: "50%",
            top: "50%",
            transform: "translate(-50%, -50%)",
          }}
          className="text-center"
        >
          <Card.Header>Select a login option</Card.Header>
          <Card.Body>
            <Container fluid>
              {env.REACT_APP_SELECT === "3" ||
              env.REACT_APP_SELECT === "" ||
              env.REACT_APP_SELECT == null ? (
                <Col>
                  <Row>
                    <Button
                      style={{ width: "100%" }}
                      size="lg"
                      variant="outline-danger"
                      onClick={() => this.setRedirect("/Instagram")}
                    >
                      INSTAGRAM
                    </Button>
                  </Row>
                  <Row style={{ marginTop: "20px" }}>
                    <Button
                      style={{ width: "100%" }}
                      size="lg"
                      variant="outline-primary"
                      onClick={() => this.setRedirect("/LinkedIn")}
                    >
                      LinkedIN
                    </Button>
                  </Row>
                </Col>
              ) : env.REACT_APP_SELECT === "1" ? (
                <Button
                  size="lg"
                  variant="outline-primary"
                  onClick={() => this.setRedirect("/LinkedIn")}
                >
                  LinkedIN
                </Button>
              ) : (
                <Button
                  size="lg"
                  variant="outline-danger"
                  onClick={() => this.setRedirect("/Instagram")}
                >
                  INSTAGRAM
                </Button>
              )}
            </Container>
          </Card.Body>
        </Card>
        <Card title="Select login options"></Card>
      </div>
    );
  }
}
